@media screen and (width >= 544px) {
  .timeline-comment .avatar {
    margin-right: 16px;
    display: block;
  }

  .timeline-comment .comment:after, .timeline-comment .comment:before {
    pointer-events: none;
    content: " ";
    border-style: solid solid outset;
    border-color: #0000;
    width: 0;
    height: 0;
    display: block;
    position: absolute;
    top: 11px;
    left: -16px;
    right: 100%;
  }

  .timeline-comment .comment:after {
    border-width: 7px;
    border-right-color: #14171c;
    margin-top: 1px;
    margin-left: 2px;
  }

  .timeline-comment .comment:before {
    border-width: 8px;
    border-right-color: #1d1f21;
  }

  .timeline-comment.current-user .comment:after, .timeline-comment.current-user .comment:before {
    pointer-events: none;
    content: " ";
    border-style: solid solid outset;
    border-color: #0000;
    width: 0;
    height: 0;
    display: block;
    position: absolute;
    top: 11px;
    left: -16px;
    right: 100%;
  }

  .timeline-comment.current-user .comment:after {
