    margin-top: 1px;
    margin-left: 2px;
  }

  .timeline-comment.current-user .comment:before {
    border-width: 8px;
    border-right-color: #1d1f21;
  }

  .timeline-comment .reactions-popover {
    display: inline-block;
  }
