.zigzag {
  z-index: 1;
  background: #14171c;
  height: 16px;
  position: relative;
}

.zigzag:before, .zigzag:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}

.zigzag:before {
  background-color: #0000;
  background-image: linear-gradient(-135deg, #1d1f21 8px, #0000 0), linear-gradient(135deg, #1d1f21 8px, #0000 0);
  background-position: 0 0;
  background-repeat: repeat-x;
  background-size: 16px 16px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  height: 12px;
  top: calc(110% - 16px);
}

.zigzag:after {
  background-color: #0000;
  background-image: linear-gradient(-135deg, #14171c 8px, #0000 0), linear-gradient(135deg, #14171c 8px, #0000 0);
  background-position: 0 0;
  background-repeat: repeat-x;
  background-size: 16px 16px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  height: 16px;
  top: calc(100% - 16px);
}

